body {
    background-color: #4EA5D9;
    
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image {
    width: 250px;
    margin-top: 50px;
    margin-bottom: 50px;
}

@media only screen and (max-width: 770px) {
    .container {
      margin: 0 10%;
    }
  
    .image {
      width: 100%;
    }
  }
 
